"use client";

import { useState } from "react";
import ChessBoardClient from "./ChessBoardClient";

export default function Page() {
  const [selectedElo, setSelectedElo] = useState<number | null>(null);

  const bots = [
    { name: "Bot Novato", elo: 500 },
    { name: "Bot Intermedio", elo: 1200 },
    { name: "Bot Avanzado", elo: 1800 },
    { name: "Bot Maestro", elo: 2200 },
    { name: "Bot Gran Maestro", elo: 2600 },
    { name: "Bot Leyenda", elo: 2900 },
  ];

  return (
    <div style={{ display: "flex", flexDirection: "column", alignItems: "center", marginTop: 50 }}>
      <h1 style={{ fontSize: 32, marginBottom: 20 }}>鮫勇 Mi Chess Online</h1>

      {!selectedElo ? (
        <>
          <h2 style={{ marginBottom: 10 }}>Elige tu bot:</h2>
          <div style={{ display: "flex", flexDirection: "column", gap: 10 }}>
            {bots.map((bot) => (
              <button
                key={bot.elo}
                onClick={() => setSelectedElo(bot.elo)}
                style={{
                  padding: "10px 20px",
                  fontSize: 16,
                  borderRadius: 8,
                  backgroundColor: "#0070f3",
                  color: "#fff",
                  cursor: "pointer",
                }}
              >
                {bot.name} - ELO {bot.elo}
              </button>
            ))}
          </div>
        </>
      ) : (
        <>
          <p style={{ marginBottom: 10, fontSize: 18 }}>Jugando contra bot ELO {selectedElo}</p>
          <ChessBoardClient botElo={selectedElo} />
          <button
            onClick={() => setSelectedElo(null)}
            style={{ marginTop: 20, padding: "8px 12px", borderRadius: 8 }}
          >
            游댃 Volver a la selecci칩n
          </button>
        </>
      )}
    </div>
  );
}
