"use client";

import { useState, useEffect } from "react";
import { Chess } from "chess.js";
import { Chessboard } from "react-chessboard";

interface Props {
  botElo: number;
}

export default function ChessBoardClient({ botElo }: Props) {
  const [game, setGame] = useState<Chess | null>(null);
  const [fen, setFen] = useState("start");
  const [message, setMessage] = useState("");

  useEffect(() => {
    const newGame = new Chess();
    setGame(newGame);
    setFen(newGame.fen());
  }, []);

  const onPieceDrop = (source: string, target: string) => {
    if (!game) return false;

    const move = game.move({ from: source, to: target, promotion: "q" });
    if (!move) return false;

    setFen(game.fen());

    const lastMove = game.history({ verbose: true }).slice(-1)[0];
    if (lastMove && lastMove.color === "w" && lastMove.captured) {
      setMessage(`Tu pieza captur칩 a ${lastMove.captured}!`);
    }

    // Bot responde seg칰n su ELO (m치s alto = m치s r치pido / inteligente)
    setTimeout(() => botMove(), 500 - Math.min(botElo / 10, 400));
    return true;
  };

  const botMove = () => {
    if (!game) return;
    const moves = game.moves();
    if (moves.length === 0) return;

    // Bot b치sico: mueve aleatorio, m치s inteligente si ELO alto (puede mejorar luego)
    const randomMove = moves[Math.floor(Math.random() * moves.length)];
    game.move(randomMove);
    setFen(game.fen());

    if (game.in_check()) setMessage("Bot est치 en jaque!");
    else setMessage("");
  };

  const resetGame = () => {
    if (!game) return;
    game.reset();
    setFen(game.fen());
    setMessage("");
  };

  if (!game) return <div>Cargando tablero...</div>;

  return (
    <div style={{ display: "flex", flexDirection: "column", alignItems: "center", marginTop: 20 }}>
      <Chessboard position={fen} onPieceDrop={onPieceDrop} boardWidth={480} arePiecesDraggable={true} />
      <p style={{ marginTop: 10, fontWeight: "bold" }}>{message}</p>
      <div style={{ marginTop: 20 }}>
        <button onClick={resetGame} style={{ padding: "8px 12px", borderRadius: 8 }}>
          游댃 Reiniciar
        </button>
      </div>
    </div>
  );
}
